/**
 * ⭐ Advanced Quality Management System
 * نظام إدارة الجودة المتقدم الذكي المتكامل
 */

import apiClient from './utils/api-client.js';
import { API_ENDPOINTS } from './config/api.js';
import connectionManager from './utils/connection-manager.js';
import realtimeSync from './utils/realtime-sync.js';
import systemEnhancer from './utils/system-enhancer.js';
import aiAssistant from './utils/ai-assistant.js';
import advancedCache from './utils/advanced-cache.js';

class AdvancedQualityManagement {
  constructor(container) {
    this.container = container;
    this.useAPI = true;
    this.apiClient = apiClient;
    this.API_ENDPOINTS = API_ENDPOINTS.advancedQuality || {};
    this.connectionManager = connectionManager;
    this.realtimeSync = realtimeSync;
    this.systemEnhancer = systemEnhancer;
    this.aiAssistant = aiAssistant;
    this.advancedCache = advancedCache;

    this.standards = [];
    this.audits = [];
    this.nonConformances = [];
    this.improvements = [];
    this.metrics = [];
    this.certifications = [];
    this.analytics = [];

    this.currentView = 'standards';
    this.filters = {
      status: 'all',
      category: 'all',
      search: '',
    };

    this.init();
  }

  async init() {
    this.render();
    this.setupEventListeners();
    await this.loadData();
    this.setupRealtimeSync();
    this.setupConnectionMonitoring();
  }

  render() {
    if (!this.container) return;

    this.container.innerHTML = `
      <div class="advanced-quality-management">
        <div class="quality-header">
          <h2>⭐ نظام إدارة الجودة المتقدم الذكي المتكامل</h2>
          <div class="header-actions">
            <button class="btn btn-primary" onclick="this.createStandard()">
              <i class="fas fa-plus"></i> معيار جديد
            </button>
            <button class="btn btn-secondary" onclick="this.createAudit()">
              <i class="fas fa-clipboard-check"></i> تدقيق جديد
            </button>
          </div>
        </div>

        <div class="quality-tabs">
          <button class="tab-btn ${this.currentView === 'standards' ? 'active' : ''}" 
                  onclick="this.switchView('standards')">
            <i class="fas fa-star"></i> المعايير
          </button>
          <button class="tab-btn ${this.currentView === 'audits' ? 'active' : ''}" 
                  onclick="this.switchView('audits')">
            <i class="fas fa-clipboard-check"></i> التدقيقات
          </button>
          <button class="tab-btn ${this.currentView === 'nonConformances' ? 'active' : ''}" 
                  onclick="this.switchView('nonConformances')">
            <i class="fas fa-exclamation-circle"></i> عدم المطابقة
          </button>
          <button class="tab-btn ${this.currentView === 'improvements' ? 'active' : ''}" 
                  onclick="this.switchView('improvements')">
            <i class="fas fa-chart-line"></i> التحسينات
          </button>
          <button class="tab-btn ${this.currentView === 'metrics' ? 'active' : ''}" 
                  onclick="this.switchView('metrics')">
            <i class="fas fa-tachometer-alt"></i> المقاييس
          </button>
          <button class="tab-btn ${this.currentView === 'certifications' ? 'active' : ''}" 
                  onclick="this.switchView('certifications')">
            <i class="fas fa-certificate"></i> الشهادات
          </button>
          <button class="tab-btn ${this.currentView === 'analytics' ? 'active' : ''}" 
                  onclick="this.switchView('analytics')">
            <i class="fas fa-chart-bar"></i> التحليلات
          </button>
        </div>

        <div class="quality-filters">
          <select class="filter-select" onchange="this.handleFilterChange('status', event)">
            <option value="all">جميع الحالات</option>
            <option value="active">نشط</option>
            <option value="inactive">غير نشط</option>
            <option value="pending">قيد الانتظار</option>
            <option value="approved">موافق عليه</option>
          </select>
          <input type="text" class="search-input" placeholder="بحث..." 
                 oninput="this.handleSearch(event)">
        </div>

        <div class="quality-content" id="qualityContent">
          ${this.renderCurrentView()}
        </div>
      </div>
    `;
  }

  renderCurrentView() {
    switch (this.currentView) {
      case 'standards':
        return this.renderStandards();
      case 'audits':
        return this.renderAudits();
      case 'nonConformances':
        return this.renderNonConformances();
      case 'improvements':
        return this.renderImprovements();
      case 'metrics':
        return this.renderMetrics();
      case 'certifications':
        return this.renderCertifications();
      case 'analytics':
        return this.renderAnalytics();
      default:
        return this.renderStandards();
    }
  }

  renderStandards() {
    const filtered = this.getFilteredData(this.standards);

    if (filtered.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-star"></i>
          <p>لا توجد معايير</p>
          <button class="btn btn-primary" onclick="this.createStandard()">
            إضافة معيار جديد
          </button>
        </div>
      `;
    }

    return `
      <div class="standards-grid">
        ${filtered
          .map(
            standard => `
          <div class="standard-card status-${standard.status}">
            <div class="standard-header">
              <h3>${standard.name || 'معيار'}</h3>
              <span class="status-badge status-${standard.status}">${this.getStatusText(standard.status)}</span>
            </div>
            <div class="standard-body">
              <div class="standard-details">
                <div class="detail-item">
                  <span class="detail-label">الفئة:</span>
                  <span class="detail-value">${standard.category || 'غير محدد'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">الإصدار:</span>
                  <span class="detail-value">${standard.version || '1.0'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">تاريخ الإنشاء:</span>
                  <span class="detail-value">${this.formatDate(standard.createdAt)}</span>
                </div>
              </div>
              ${
                standard.description
                  ? `
                <div class="standard-description">
                  <p>${standard.description.substring(0, 150)}${standard.description.length > 150 ? '...' : ''}</p>
                </div>
              `
                  : ''
              }
            </div>
            <div class="standard-actions">
              <button class="btn btn-sm btn-primary" onclick="this.viewStandard(${standard.id})">
                <i class="fas fa-eye"></i> عرض
              </button>
              <button class="btn btn-sm btn-secondary" onclick="this.editStandard(${standard.id})">
                <i class="fas fa-edit"></i> تعديل
              </button>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  renderAudits() {
    if (this.audits.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-clipboard-check"></i>
          <p>لا توجد تدقيقات</p>
          <button class="btn btn-primary" onclick="this.createAudit()">
            إضافة تدقيق جديد
          </button>
        </div>
      `;
    }

    return `
      <div class="audits-list">
        ${this.audits
          .map(
            audit => `
          <div class="audit-card status-${audit.status}">
            <div class="audit-header">
              <h3>${audit.title || 'تدقيق'}</h3>
              <span class="status-badge status-${audit.status}">${this.getStatusText(audit.status)}</span>
            </div>
            <div class="audit-body">
              <div class="audit-details">
                <div class="detail-item">
                  <span class="detail-label">النوع:</span>
                  <span class="detail-value">${audit.type || 'غير محدد'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">التاريخ:</span>
                  <span class="detail-value">${this.formatDate(audit.auditDate)}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">المدقق:</span>
                  <span class="detail-value">${audit.auditorName || 'غير محدد'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">النتيجة:</span>
                  <span class="detail-value">${audit.result || 'غير محدد'}</span>
                </div>
              </div>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  renderNonConformances() {
    if (this.nonConformances.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-exclamation-circle"></i>
          <p>لا توجد حالات عدم مطابقة</p>
        </div>
      `;
    }

    return `
      <div class="non-conformances-list">
        ${this.nonConformances
          .map(
            nc => `
          <div class="nc-card severity-${nc.severity || 'medium'}">
            <div class="nc-header">
              <h3>${nc.title || 'عدم مطابقة'}</h3>
              <span class="severity-badge severity-${nc.severity || 'medium'}">
                ${this.getSeverityText(nc.severity || 'medium')}
              </span>
            </div>
            <div class="nc-body">
              <div class="nc-details">
                <div class="detail-item">
                  <span class="detail-label">المعيار:</span>
                  <span class="detail-value">${nc.standardName || 'غير محدد'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">التاريخ:</span>
                  <span class="detail-value">${this.formatDate(nc.identifiedDate)}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">الحالة:</span>
                  <span class="detail-value">${this.getStatusText(nc.status)}</span>
                </div>
              </div>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  renderImprovements() {
    if (this.improvements.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-chart-line"></i>
          <p>لا توجد تحسينات</p>
        </div>
      `;
    }

    return `
      <div class="improvements-list">
        ${this.improvements
          .map(
            improvement => `
          <div class="improvement-card status-${improvement.status}">
            <div class="improvement-header">
              <h3>${improvement.title || 'تحسين'}</h3>
              <span class="status-badge status-${improvement.status}">${this.getStatusText(improvement.status)}</span>
            </div>
            <div class="improvement-body">
              <div class="improvement-details">
                <div class="detail-item">
                  <span class="detail-label">النوع:</span>
                  <span class="detail-value">${improvement.type || 'غير محدد'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">تاريخ البدء:</span>
                  <span class="detail-value">${this.formatDate(improvement.startDate)}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">التقدم:</span>
                  <span class="detail-value">${improvement.progress || 0}%</span>
                </div>
              </div>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  renderMetrics() {
    if (this.metrics.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-tachometer-alt"></i>
          <p>لا توجد مقاييس</p>
        </div>
      `;
    }

    return `
      <div class="metrics-grid">
        ${this.metrics
          .map(
            metric => `
          <div class="metric-card">
            <div class="metric-header">
              <h3>${metric.name || 'مقياس'}</h3>
              <span class="metric-value">${metric.value || 0} ${metric.unit || ''}</span>
            </div>
            <div class="metric-body">
              <div class="metric-details">
                <div class="detail-item">
                  <span class="detail-label">الهدف:</span>
                  <span class="detail-value">${metric.target || 0} ${metric.unit || ''}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">التاريخ:</span>
                  <span class="detail-value">${this.formatDate(metric.date)}</span>
                </div>
              </div>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  renderCertifications() {
    if (this.certifications.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-certificate"></i>
          <p>لا توجد شهادات</p>
        </div>
      `;
    }

    return `
      <div class="certifications-grid">
        ${this.certifications
          .map(
            cert => `
          <div class="cert-card status-${cert.status}">
            <div class="cert-header">
              <h3>${cert.name || 'شهادة'}</h3>
              <span class="status-badge status-${cert.status}">${this.getStatusText(cert.status)}</span>
            </div>
            <div class="cert-body">
              <div class="cert-details">
                <div class="detail-item">
                  <span class="detail-label">المؤسسة:</span>
                  <span class="detail-value">${cert.issuer || 'غير محدد'}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">تاريخ الإصدار:</span>
                  <span class="detail-value">${this.formatDate(cert.issueDate)}</span>
                </div>
                <div class="detail-item">
                  <span class="detail-label">تاريخ الانتهاء:</span>
                  <span class="detail-value">${this.formatDate(cert.expiryDate) || 'غير محدد'}</span>
                </div>
              </div>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  renderAnalytics() {
    if (this.analytics.length === 0) {
      return `
        <div class="empty-state">
          <i class="fas fa-chart-bar"></i>
          <p>لا توجد بيانات تحليلية</p>
        </div>
      `;
    }

    return `
      <div class="analytics-dashboard">
        ${this.analytics
          .map(
            analytic => `
          <div class="analytic-card">
            <div class="analytic-header">
              <h3>${analytic.metric}</h3>
              <span class="analytic-value">${analytic.value}</span>
            </div>
            <div class="analytic-body">
              <p>${analytic.description || ''}</p>
            </div>
          </div>
        `
          )
          .join('')}
      </div>
    `;
  }

  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.category !== 'all') {
      filtered = filtered.filter(item => item.category === this.filters.category);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.name && item.name.toLowerCase().includes(search)) ||
          (item.title && item.title.toLowerCase().includes(search)) ||
          (item.description && item.description.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [standards, audits, nonConformances, improvements, metrics, certifications, analytics] =
        await Promise.all([
          this.apiClient.get(this.API_ENDPOINTS.standards || '/api/advanced-quality/standards'),
          this.apiClient.get(this.API_ENDPOINTS.audits || '/api/advanced-quality/audits'),
          this.apiClient.get(
            this.API_ENDPOINTS.nonConformances || '/api/advanced-quality/non-conformances'
          ),
          this.apiClient.get(
            this.API_ENDPOINTS.improvements || '/api/advanced-quality/improvements'
          ),
          this.apiClient.get(this.API_ENDPOINTS.metrics || '/api/advanced-quality/metrics'),
          this.apiClient.get(
            this.API_ENDPOINTS.certifications || '/api/advanced-quality/certifications'
          ),
          this.apiClient.get(this.API_ENDPOINTS.analytics || '/api/advanced-quality/analytics'),
        ]);

      this.standards = standards.data || [];
      this.audits = audits.data || [];
      this.nonConformances = nonConformances.data || [];
      this.improvements = improvements.data || [];
      this.metrics = metrics.data || [];
      this.certifications = certifications.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading quality data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-quality', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.filters.search = event.target.value;
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('qualityContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

  getStatusText(status) {
    const statusMap = {
      active: 'نشط',
      inactive: 'غير نشط',
      pending: 'قيد الانتظار',
      approved: 'موافق عليه',
      draft: 'مسودة',
      completed: 'مكتمل',
      inprogress: 'قيد التنفيذ',
      valid: 'صالح',
      expired: 'منتهي',
    };
    return statusMap[status] || status;
  }

  getSeverityText(severity) {
    const severityMap = {
      low: 'منخفض',
      medium: 'متوسط',
      high: 'عالي',
      critical: 'حرج',
    };
    return severityMap[severity] || severity;
  }

  formatDate(date) {
    if (!date) return 'غير محدد';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedStandards', JSON.stringify(this.standards));
      localStorage.setItem('advancedAudits', JSON.stringify(this.audits));
      localStorage.setItem('advancedNonConformances', JSON.stringify(this.nonConformances));
      localStorage.setItem('advancedImprovements', JSON.stringify(this.improvements));
      localStorage.setItem('advancedMetrics', JSON.stringify(this.metrics));
      localStorage.setItem('advancedCertifications', JSON.stringify(this.certifications));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.standards = JSON.parse(localStorage.getItem('advancedStandards') || '[]');
      this.audits = JSON.parse(localStorage.getItem('advancedAudits') || '[]');
      this.nonConformances = JSON.parse(localStorage.getItem('advancedNonConformances') || '[]');
      this.improvements = JSON.parse(localStorage.getItem('advancedImprovements') || '[]');
      this.metrics = JSON.parse(localStorage.getItem('advancedMetrics') || '[]');
      this.certifications = JSON.parse(localStorage.getItem('advancedCertifications') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createStandard = this.createStandard.bind(this);
    this.createAudit = this.createAudit.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewStandard = this.viewStandard.bind(this);
    this.editStandard = this.editStandard.bind(this);
  }

  async createStandard() {
    console.log('Create standard');
  }

  async createAudit() {
    console.log('Create audit');
  }

  async viewStandard(id) {
    console.log('View standard', id);
  }

  async editStandard(id) {
    console.log('Edit standard', id);
  }
}

export default AdvancedQualityManagement;
